<template>
  <div class="container tuto">
    <h1>Tutorial</h1>

    <p>
      Incoming
    </p>

    <b-card-group class="justify-content-center">
      <div class="col-12">
        <b-card class="bg-transparent" header="Common" header-bg-variant="dark" header-text-variant="white">
          <div>
            <p>Pif</p>
            <div class="container">
              <b-table striped bordered small head-variant="dark" hover :items="[{one: 1, two: 2}]"></b-table>
            </div>
            <b-card no-body class="bg-transparent border-0">
              <b-card-header header-tag="header" class="p-1 border-0" role="tab">
                <b-btn block href="#" v-b-toggle.accordionSample variant="info">Accordion</b-btn>
              </b-card-header>
              <b-collapse id="accordionSample" visible accordion="process-accordion" role="tabpanel">
                <b-card-body>
                  <p class="card-text">
                    Paf
                  </p>
                  <b-card-group deck class="justify-content-center">
                    <b-card no-body header="Inside">
                      <p>Text</p>
                    </b-card>
                  </b-card-group>
                </b-card-body>
              </b-collapse>
            </b-card>
          </div>
        </b-card>
      </div>

    </b-card-group>

  </div>
</template>

<script>
export default {
  name: 'Tutorial',
  data () {
    return {
    }
  }
}
</script>

<style lang="scss">

  .tuto p,dl {
    text-align: justify;
    dt {
      float: left;
      margin-right: 1rem;
    }
  }

</style>



